@use "sass:math";

$circle-size: 16px; // Use pairs.
$circle-bar-spacing: 6px; // Use pairs.

@include body-class(true) {

	.progressbar-container {
		height: #{$circle-size - $circle-bar-spacing};
		display: block;
		position: relative;
		/*margin: $sui-gutter 0;*/
		margin: 12px 0;
		border-radius: $circle-size;
		background-color: palette(gray, lighter);

		.progressbar-status {
			height: #{$circle-size - $circle-bar-spacing};
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			border-radius: $circle-size;
			background-color: $blue;
		}

		.progress-circles {
			width: 100%;
			display: flex;
			position: absolute;
			justify-content: space-evenly;
			top: -#{math.div($circle-bar-spacing, 2)};
			left: 0;
			margin: 0;
			padding: 0;
			border: 0;

			&, li {
				list-style: none;
			}

			li.circle {
				width: $circle-size;
				height: $circle-size;
				cursor: auto;
				pointer-events: none;
				flex: 0 0 auto;
				margin: 0;
				padding: 0;
				border-radius: 50%;
				background-color: palette(gray, lighter);

				[class*="sui-icon-"] {
					width: $circle-size;
					height: $circle-size;
					opacity: 0;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 8px;
					text-align: center;

					&:before {
						min-width: 1px;
						display: block;
						flex: 1;
						position: relative;
						top: 1px;
						color: inherit;
					}
				}

				&.active {
					pointer-events: all;
					background-color: $blue;

					[class*="sui-icon-"] {
						opacity: 1;
						color: $white;
					}
				}
			}
		}
	}

	.step-0 .ci-step-0,
	.step-1 .ci-step-0, .step-1 .ci-step-1,
	.step-2 .ci-step-0, .step-2 .ci-step-1, .step-2 .ci-step-2,
	.step-3 .ci-step-0, .step-3 .ci-step-1, .step-3 .ci-step-2, .step-3 .ci-step-3,
	.step-4 .ci-step-0, .step-4 .ci-step-1, .step-4 .ci-step-2, .step-4 .ci-step-3, .step-4 .ci-step-4,
	.step-5 .ci-step-0, .step-5 .ci-step-1, .step-5 .ci-step-2, .step-5 .ci-step-3, .step-5 .ci-step-4, .step-5 .ci-step-5
	{
		pointer-events: all !important;
		background-color: $blue !important;

		[class*="sui-icon-"] {
			opacity: 1 !important;
			color: $white !important;
		}
	}

}

