.snapshot-modal--file__explorer {
	.sui-modal-content {
		.snapshot-modal--file__explorer__content {
			min-height: 200px;
			max-height: 400px;
			overflow-y: scroll;
			padding: 10px 15px 10px 0;
		}

		.sui-box-footer {
			.sui-button-float--right {
				position: absolute;
				right: 20px;
			}
		}
	}
}

.sui-tree {
	li {
		.sui-tree-node {
			.snapshot-icon {
				cursor: pointer !important;
			}

			.sui-node-text-right {
				font-size: 12px;
				font-weight: 500;
			}
		}

		&.node--enabled {
			.sui-node-text-right {
				color: #17A8E3;
			}
		}

		&.node--disabled {
			.sui-node-text-right {
				color: #666666;
			}
		}
	}
}