.snapshot-page-settings {

	#snapshot-configs-wrap {
		@media screen and (max-width: 370px) {
			.sui-box {
				.sui-actions-right {
					display: flex;
					justify-content: space-between;
					margin-left: 0;
				}
			}
		}
		.sui-accordion-item {
			.sui-accordion-item-title {
				@media screen and (max-width: 540px) {
					span {
						display: none;
					}
				}
				.sui-tooltip {
					margin-top: 6px;
					margin-left: 10px;

					svg {
						margin-left: 0;
					}
				}
			}

			.sui-accordion-col-auto {
				@media screen and (max-width: 540px) {
					.sui-button-ghost {
						display: none;
					}
				}
				.sui-button {
					&::before {
						content: "(";
						margin-right: 5px;
						font-family: wpmudev-plugin-icons !important;
						speak: none;
						font-style: normal;
						font-weight: 400;
						font-variant: normal;
						text-transform: none;
						line-height: normal;
						position: relative;
						top: 1px;
					}
				}
				.sui-dropdown {
					.sui-button-icon {
						.sui-icon-widget-settings-config {
							&::before{
								content: "";
							}
						}
					}
				}
			}
			.sui-accordion-item-body {
				.sui-tooltip {
					.sui-button-icon {
						width: 30px;
						border-width: 2px;
					}
				}
			}
		}
	}

	#snapshot-authentication-wrap {
	}
}

.toplevel_page_snapshot,
.snapshot-pro_page_snapshot-settings {

	.apply-loader {
		margin-bottom: 15px;
	}

	.region-match,
	.region-mismatch {
		display: none;
	}

	#sui-configs-apply-modal {
		.sui-box-footer {
			button {
				display: none;
			}
		}

		.region-loaded {
			.sui-box-footer {
				button {
					display: block;
				}
			}
		}
	}

	.region-loaded{
		&.snapshot-region-match {
			.region-mismatch {
				display: none;
			}

			.region-match {
				display: block;
				margin-bottom: 30px;
			}
		}
		&.snapshot-region-mismatch {
			.region-match {
				display: block;
			}

			.region-mismatch {
				display: block;
			}
		}

		.apply-loader {
			display: none;
		}
	}
}