.snapshot-page-hosting-backups {

	.snapshot-hosting-backups-summary {

		@media (min-width: 783px) {
			&.sui-summary:not(.sui-unbranded):not(.sui-rebranded):not(.sui-summary-sm) {
				background-image: url("../img/snapshot-hosting-hero.svg") !important;
				background-size: 252px 193px !important;
				background-position: 0 bottom !important;
			}
		}
	}

	.snapshot-page-main {

		.snapshot-hosting-backups-backups {

			.snapshot-backup-list-loader {
				padding: 150px 30px;

				.sui-icon-loader {
					margin-right: 5px;
				}

				p {
					font-size: 13px;
					color: #888888;
					font-weight: 500;
				}
			}
			.snapshot-hosting-backups-table{
				border-collapse: separate;
				
				.sui-accordion-item--open:focus-visible{
					outline: none;
				}
				.sui-box:focus{
					border: none;
				}
			}

			.sui-pagination {
				li {
					flex: 1;

					&.sui-active {
						background: #F8F8F8;
					}
				}
			}

		}

		.sui-icon-storage-server-data,
		.sui-icon-automate {
			&:before {
				color: #333333;
			}
		}

		.sui-icon-wpmudev-logo:before {
			color: #3174fa;
		}

	}

}
