.sui-hidden {
	display: none !important;
}

.sui-button--disabled {
	background-color: #E6E6E6 !important;
	color: #aaa !important;
	cursor: default !important;
}

.snapshot-form-border {
	border: 1px solid #E6E6E6;
    border-radius: 5px;
}

.justify-content-center {
	justify-content: center;
}

.justify-content--space__between {
	justify-content: space-between;
}

.w-100 {
	width: 100%;
}

.sui-width-110 {
	width: 110px;
}

.p-30 {
	padding: 30px;
}

.mt-25 {
	margin-top: 25px;
}

.sui-button--group {
	.sui-button--group__item {
		float: left;
		font-size: 13px;
		padding: 9px 20px;
		background-color: #f8f8f8;
		color: #666;
		margin: 0 1px 0 0;
		cursor: pointer;
		font: 500 13px/22px "Roboto",Arial,sans-serif;

		&:first-child {
			border-radius: 4px 0 0 4px;
		}

		&:last-child {
			border-radius: 0 4px 4px 0;
		}

		&.selected {
			background-color: #E1F6FF;
    		color: #17A8E3;
		}
	}

	&::after{
		content: " ";
		clear: both;
		display: block;
	}
}



.wpmudev-hide-branding {
	.sui-wrap {
		.sui-accordion-item {
			.sui-accordion-item-title {
				span {
					&:first-of-type {
						display: none;
					}
				}
			}
		}
	}
}
.sui-2-12-13{
	.sui-wrap {
		.sui-box-body {
			.sui-tree[data-tree=directory] {
				li.is-loading[role=treeitem] {
					& > .sui-tree-node {
						& > span[role=button] {
							&::before {
								content: "N";
							}
						}
					}
				}
			}

			.sui-form-field {
				.sui-description {
					font-size: 12px;
					line-height: 16px;
				}

				&.create-directory--toggle {
					.sui-checkbox {
						span#create-onedrive-dir-label {
							font-size: 13px !important;
							line-height: 22px;
							color: #666666;
						}
					}
				}
			}
		}

		.sui-progress-bar {
			border-radius: 5px;
		}
	}
}
.sui-wrap {
	.sui-flex {
		display: flex;

		&.align-items-center {
			align-items: center;
		}

		&.justify-content-center {
			justify-content: center;
		}
	}

	#snapshot-dashboard-configs {
		.sui-accordion-item {
			.sui-accordion-item-title {
				@media screen and (max-width: 540px) {
					span {
						display: none;
					}
				}
				.sui-tooltip {
					margin-top: 6px;
					margin-left: 10px;

					svg {
						margin-left: 0;
					}
				}
			}
		}
	}

	.justify-evenly {
		justify-content: space-evenly;
	}

	.sui-floating-notices {
		#snapshot-notice--clear__exclusion {
			p {
				a {
					color: #17A8E3;
					text-decoration: none;
				}
			}
		}
		.sui-notice {
			p {
				color: #333333 !important;
				a {
					font-weight: 500;

					&.blue-link {
						color: #17A8E3;
						text-decoration: none;
					}
				}
			}
		}
	}
	.sui-notice {
		p {
			a {
				color: inherit;
				text-decoration: underline;
			}
		}

		.notice-log-row {
			background-color: #fafafa;
			border-radius: 4px;
			padding: 9px 15px;
			color: #666666;
			font-size: 12px;

			.backup-status {
				color: #ff6d6d;
			}
		}
	}

	.sui-table {
		thead, tbody, tfoot {
			th, td {
				&.first-child {
					padding-left: #{$sui-gutter-md - $table--border-width} !important;

					@include media(min-width, md) {
						padding-left: #{$sui-gutter - $table--border-width} !important;
					}
				}

				&.last-child {
					padding-right: #{$sui-gutter-md - $table--border-width} !important;

					@include media(min-width, md) {
						padding-right: #{$sui-gutter - $table--border-width} !important;
					}
				}

				&.sui-table-item-title {
					.sui-icon-warning-alert {
						&:before {color: $notice-error-icon-color;}
					}

					&.gray {
						color: #888888;
					}
				}
			}
		}
	}

	#snapshot-uninstall-v3 {
		a {
			color: #17A8E3;
			text-decoration: none;
		}
	}

	#modal-confirm-v3-uninstall {
		h3 {
			white-space: normal;
		}
	}

	/* Dashboard page, Backups page, Hosting Backups page */
	.snapshot-dashboard-backups, .snapshot-dashboard-hosting-backups, .snapshot-page-main {
		.snapshot-row, .current-backup-row, .log-row, .hosting-backup-row {
			.snapshot-icon-tooltip {
				position: absolute;
				width: 16px;
				height: 20px;
				&:after {
					left: -30px;
				}
			}
		}

		.snapshot-icon-tooltip {
			//margin-left: 5px;
			span[class*="sui-icon-"] {
				&:before {
					color: #888888;
				}
				&:hover {
					&:before {
						color: #666666;
					}
				}
			}
		}
	}

	#snapshot-uninstall-v3 {
		.snapshot-v3-uninstall-hero {
			position: absolute;
			width: 118px;
			height: 111px;
			margin-left: 10px;
			background-color: #C7C2F2;
			border-radius: 4px;
			background-repeat: no-repeat;
			background-position: center;
			background-image: url(../img/snapshot-uninstall-v3-hero.svg);

			&.snapshot-hidden-branding {
				display: none;
			}
		}

		.snapshot-v3-uninstall-text {
			padding-left: 150px;
			padding-right: 30px;

			&.snapshot-hidden-branding {
				padding-left: 15px;
			}
		}
	}

	.snapshot-no-backups-hero {

		&.snapshot-hidden-branding {
			display: none;
		}
	}
	.sui-table {

		.destination-row {

			&.deactivated-destination{
				background-color: #f8f8f8;
				td:not(:first-child):not(:last-child) {
					opacity: 0.3;
				}
				.tooltip-background, .tpd-name {
					opacity: 0.3;
				}
				.sui-dropdown {
					.destination-edit {
						pointer-events: none;
						opacity: 0.3;
					}
				}
			}

			&.destination-type-s3_other{
				.sui-dropdown {
					.destination-view {
						display: none;
					}
				}
			}

			.sui-icon-folder {
				margin-right: 5px;
				position: relative;
				top: 2px;

				&:before {
					color: #dddddd;
				}
			}

			.destination-actions {
				display: flex;
				align-items: center;
				justify-content: flex-end;

				.sui-form-field {
					margin-bottom: 0;
				}

				.sui-dropdown {
					margin-left: 20px;
				}
			}

			.mobile-row {
				.sui-icon-wpmudev-logo {
					margin-right: 5px;
					&:before {
						color: #3174fa;
					}
				}

				.sui-table-item-title {
					word-break: break-word;

					&:last-child {
						color: #888888;
					}
				}

				.destination-name {
					padding-top: 2px;
					padding-right: 50px;

					.destination-icon {
						display: inline-block;
						margin-right: 5px;
						width: 16px;
						height: 16px;
						background-size: 16px;
						background-position: left bottom;
						background-repeat: no-repeat;
						position: relative;
						top: 3px;

						&.destination-icon-amazon-s3 {
							background-image: url(../img/destination-logo-aws-small.svg);
							top: 0;
						}

						&.destination-icon-aws,
						&.destination-icon-backblaze,
						&.destination-icon-wasabi,
						&.destination-icon-googlecloud,
						&.destination-icon-digitalocean,
						&.destination-icon-s3_other {
							background-image: url(../img/destination-logo-aws-small.svg);
							top: 0;
						}

						&.destination-icon-gdrive {
							background-image: url(../img/destination-logo-gd-small.svg);
						}

						&.destination-icon-ftp,
						&.destination-icon-sftp {
							background-image: url(../img/destination-logo-ftp-small.svg);
						}

						&.destination-icon-dropbox {
							background-image: url(../img/destination-logo-dropbox-small.svg);
						}
					}
				}

				.destination-cells {
					margin-right: -60px;

					.sui-col-xs-6 {
						padding: 10px 10px;

						.destination-path {
							display: flex;
							align-items: baseline;

							[class*="sui-icon-"] {
								margin-right: 5px;
							}
						}
					}
				}
			}

			.destination-actions-cell {
				@include media(max-width, md) {
					vertical-align: top;
				}
			}

		}

		.row-icon {
			padding-left: 45px !important;

			.tooltip-container {
				display: flex;
				align-items: center;

				.tooltip-background {
					position: absolute;
					left: 30px;
					min-width: 30px;
					height: 30px;
					background-size: 30px;
					background-repeat: no-repeat;
				}

				.tooltip-block {
					min-width: 30px;
					height: 30px;
					position: relative;
					left: -15px;
				}

				.destination-title {
					display: inline-block;
					min-width: 75px;
				}
			}
		}
		.row-icon.row-icon-wpmudev .tooltip-background:not(.custom-icon) {
			background-image: url(../img/wpmudev-logo.svg);
		}
		.row-icon.row-icon-wpmudev .tooltip-background.custom-icon::before {
			font-size: 30px;
		}
		.custom-icon-sm {
			display: inline-block;
			margin-right: 10px;
			color: #3174fa;
		}
		.custom-icon-sm.icon-linked {
			width: 15px;
			height: 15px;
			background-size: contain;
			margin-bottom: -3px;
		}
		.row-icon.row-icon-aws, .row-icon.row-icon-googlecloud, .row-icon.row-icon-digitalocean, .row-icon.row-icon-backblaze, .row-icon.row-icon-wasabi, .row-icon.row-icon-s3_other {
			.tooltip-background {
				background-image: url(../img/destination-logo-aws.svg);
			}
		}
		.row-icon.row-icon-dropbox .tooltip-background {
			background-image: url(../img/destination-logo-dropbox.svg);
		}
		.row-icon.row-icon-gdrive .tooltip-background {
			background-image: url(../img/destination-logo-google-drive.svg);
		}
		.row-icon.row-icon-ftp .tooltip-background {
			background-image: url(../img/destination-logo-ftp.svg);
		}
		.row-icon.row-icon-onedrive .tooltip-background {
			background-image: url(../img/destination-logo-onedrive.svg);
		}
	}

	.sui-select-dropdown-container--open {
		// Fix overlapping sui-select dropdown list with buttons in the modal
		z-index: 1;
	}

	.snapshot-storage-widget {
		width: fit-content;

		.snapshot-storage-used {
			color: #333333;
			font-weight: 500;
			line-height: 16px;
			font-size: 17px;

			.snapshot-storage-used-label {
				font-size: 11px;
			}
		}

		.snapshot-storage-progress {
			.sui-progress {
				display: inline-block;
				margin-top: 10px;
				width: 80px;
			}

			.storage-action {
				display: inline-block;
				margin-left: 6px;
				font-size: 13px;
				line-height: 16px;
				position: relative;
				bottom: 1px;

				&.snapshot-storage-progress-add {
					display: none;
				}
			}
		}

		&.full {
			.storage-action {
				&.snapshot-storage-progress-manage {
					display: none;
				}
				&.snapshot-storage-progress-add {
					display: inline-block;
				}
			}

			.sui-progress-bar{
				border-radius: 5px;

				& > span {
					background: #ff6d6d;
				}
			}

		}
	}

}
