@import 'components/explorer';

@media screen and (max-width: 1366px) {
	.view-log--text {
		display: none !important;

		+.view-log {
			display: block !important;
		}
	}
}

@media screen and (min-width: 1367px) {
	.view-log--text {
		display: block !important;

		+.view-log{
			display: none !important;
		}
	}
}

.sui-2-12-13 {

	.sui-wrap.snapshot-page-backups {
		pre.snapshot-ips-snippet {
			border: none;
		}

		#modal-snapshot-update-backup-comment {
			.sui-box {
				.sui-box-body {
					padding-bottom: 60px;
				}
			}
		}
	}
}

.snapshot-page-backups {

	.sui-notice-content {
		span.snapshot-unwrittable-files-notice, span.snapshot-unwrittable-tables-notice {
			display: block;

			.snapshot-unwrittable-files-list, .snapshot-unwrittable-tables-list {
				display: block;
				font-weight: 500;
				margin-top: 10px;
				font-size: 12px;
			}

			.snapshot-many-unwrittable-files-notice, .snapshot-many-unwrittable-tables-notice {
				display: block;
				margin-bottom: 10px;
			}

			.snapshot-many-unwrittable-files-list, .snapshot-many-unwrittable-tables-list{
				font-weight: 500;
				font-size: 12px;
			}

			.snapshot-many-unwrittable-files-more, .snapshot-many-unwrittable-tables-more {
				font-size: 12px;
				font-weight: 500;
				color: #666666;
			}
		}
	}

    .snapshot-backups-summary {

        @media (min-width: 783px) {
            &.sui-summary:not(.sui-unbranded):not(.sui-rebranded):not(.sui-summary-sm) {
                background-image: url(../img/snapshot-backups-hero-2.svg) !important;
                background-size: 235px !important;
                background-position: 10px bottom !important;
                background-repeat: no-repeat !important;


                .sui-summary-image-space {
                    min-height: 143px !important;
                }
            }
        }

        .sui-summary-details.snapshot-backups-number {

            [class*="sui-icon-"]{
                top: 0;
                left: 0;
            }
        }
	}

	.snapshot-exclusions-settings-box {
		border: 1px solid #E6E6E6;
		border-radius: 5px;
		position: relative;

		.sui-accordion-item-action {
			margin-right: 5px;
		}

		ul {
			list-style: disc;
			list-style-position: inside;
			margin-left: 20px;
			margin-bottom: 30px;
		}

		.sui-accordion:not(.sui-table):not(.sui-accordion-block):not(.sui-builder-fields):last-child{
			border: none;
			border-bottom: 1px solid #E6E6E6;
			border-radius: 0;

			.sui-accordion-item-header {
				padding: 30px 0;
			}

			.sui-accordion-item-body {
				color: #888888;

				p {
					color: #888888;
				}

				.snapshot-disabled-exclusions {
					opacity: 35%;
				}
			}
		}

		.snapshot-exclusions-helper a {
			font-size: 95%;
		}

		.snapshot-exclusion-titles {
			.snapshot-exclusion-title {
				font-size: 15px;
				color: #666666;
				font-weight: 500;
			}
		}

		.snapshot-global-exclusions-field {
			padding: 0 30px 30px;

			.sui-multistrings-list {
				height: 120px;
				max-height: 121px;
				overflow-y: scroll;
				display: block;
				scroll-behavior: smooth;
				li {
					display: inline-flex;
					word-break: break-all;
				}
				li[title] {
					display: inline-flex;
				}

			}
		}

		div.snapshot-db-exclusions {
			padding-bottom: 25px;
			max-height: 360px;
    		overflow-y: scroll;

			.snapshot-tables-listitem {
				display: inline-block !important;
				width: 48%;
			}

			.search_hide {
				display: none !important;
			}

			ul.sui-tree>li {
				padding-left: 29px !important;
			}
		}

		.search-form-field.sui-icon-search {
			position: relative;
			margin-right: 0px !important;
		}
		.search{
			padding-left: 40px;
		}
		.search-form-field::after {
			content: "";
			width: 30px;
			position: absolute;
			top: 50%;
			left: 38px;
			transform: translateY(-50%);
			text-align: center;
		}
		.tables-search-continer {
			padding: 0 30px 0px;
		}
		a#reset-tables-search {
			position: absolute;
			right: 20px;
			z-index: 9;
			background-color: white;
		}
		a#reset-tables-search:hover {
			background-color: #888;
		}
		p#tables-search-text-no-result{
			margin-top: 0;
		}
	}

	#global-exclusions {
		.sui-active-filter {
			[class*="sui-icon-"].sui-sm {
				padding-right: 5px;
				&:before {
					color: #ffffff;
				}
			}
		}
	}

    .snapshot-page-main {

		.sui-icon-snapshot,
		.sui-icon-automate {
			&:before {
				color: #333333;
			}
		}

		.sui-icon-wpmudev-logo:before {
			color: #3174fa;
		}

		.sui-table {
			.sui-table-item-title {
				color: #333333;
			}
		}

        .snapshot-list-backups {

            .sui-notice {
                margin-bottom: 10px;
            }

            .snapshot-no-backups {
				display: none;
            }

            .snapshot-backup-list-loader {
                padding: 150px 30px;

                .sui-icon-loader {
                    margin-right: 5px;
                }

                p {
					font-size: 13px;
					color: #888888;
					font-weight: 500;
                }
            }

            .snapshot-listed-backups {
                display: none;

				.snapshot-listed-backups-header {
					p {
						margin-bottom: 0;
					}
				}

				th {
					height: 30px;
					margin-top: 30px;
				}

				.snapshot-row {
					.snapshot-backup--name {
						.sui-description {
							margin-top: 0;
						}
					}
				}

				.snapshot-row.snapshot-has-comment {
					height: 81px;

					.snapshot-backup--name  {
						.sui-description {
							margin-left: 28px;
						}
					}
				}

				.snapshot-row.snapshot-failed-backup {
					border-left: 2px solid $notice-error-icon-color;

					.view-log {
						float: right;
						margin-right: -8px;
					}
					.snapshot-delete-backup {
						float: right;
						margin-right: 10px;
					}
					.frequency {
						position: relative;
						top: 4px;
					}
				}

				.snapshot-destination-loader {
					margin-right: 10px;
					font-size: 16px;
					vertical-align: middle;
				}

				.current-backup-row.sui-accordion-item--open {
					.mobile-row {
						.running-backup-desc {
							display: none;
						}
					}
				}

				.sui-checkbox {
					span {
						margin-bottom: 3px;
					}
				}
				.sui-table-item-title {
					.sui-checkbox {
						span {
							margin-bottom: 5px;
						}
					}
				}

				.snapshot-details-row {
					outline: none;

					.snapshot-export-notice {
						margin-bottom: 30px;
					}
					.sui-settings-label {
						font-size: 13px;
						line-height: 22px;
						font-weight: 500;
						color: #333333;

					}
					.sui-description {
						font-size: 13px;
						line-height: 22px;
						font-weight: 500;
						color: #888888;

						.ellipsis {
							overflow: hidden;
							white-space: nowrap;
							text-overflow: ellipsis;

							&.others-txt {
								color: #17A8E3;
								cursor: default;
							}
						}

						.sui-tooltip {
							&:before {
								left: 30px;
							}
						}

						a {
							color: #888888;
							&:hover {
								color: #17a8e3;
								[class*="sui-icon-"]:before {
									color: #17a8e3;
								}
							}
						}

					}
					[class*="sui-icon-"].left {
						margin-right: 8px;
					}
					[class*="sui-icon-"].right {
						margin-left: 8px;
					}
					[class*="sui-icon-"].center {
						margin-left: 8px;
						margin-right: 8px;
					}
					.sui-button-red {
						background-color: #ffe5e9;	// Delete
					}
					.sui-button-default {
						background-color: #f8f8f8;	// Restore
						border-color: #f8f8f8;
						&:hover {
							background-color: #888888;
							border-color: #888888;
						}
					}

					.snapshot-delete-backup {
						display: none;
					}
					.snapshot-delete-backup-inactive {
						display: block;
					}
					&.snapshot-last-backup {
						.snapshot-delete-backup {
							display: block;
						}
						.snapshot-delete-backup-inactive {
							display: none;
						}
					}
				}

				.snapshot-non-restoration {
					margin-bottom: 0px;

					&:focus {
						border: none;
					}
					.sui-box-body {
						padding-bottom: 0px;

						.sui-col-md-3 {
							margin-bottom: 30px;
						}

						.snapshot-s3-export-backup-details {
							background-image: url(../img/destination-logo-aws-small.svg);
							background-size: 16px;
							background-position: left;
							background-repeat: no-repeat;
							padding-left: 25px;
							font-weight: 500;
							color: #888888;
							margin-right: 3px;
						}

						.snapshot-gdrive-export-backup-details {
							background-image: url(../img/destination-logo-gd-small.svg);
							background-size: 16px;
							background-position: left;
							background-repeat: no-repeat;
							padding-left: 25px;
							font-weight: 500;
							color: #888888;
							margin-right: 3px;
						}

						.snapshot-dropbox-export-backup-details {
							background-image: url(../img/destination-logo-dropbox-small.svg);
							background-size: 16px;
							background-position: left;
							background-repeat: no-repeat;
							padding-left: 25px;
							font-weight: 500;
							color: #888888;
							margin-right: 3px;
						}

						.snapshot-ftp-export-backup-details,
						.snapshot-sftp-export-backup-details {
							background-image: url(../img/destination-logo-ftp-small.svg);
							background-size: 16px;
							background-position: left;
							background-repeat: no-repeat;
							padding-left: 25px;
							font-weight: 500;
							color: #888888;
							margin-right: 3px;
						}

						.snapshot-onedrive-export-backup-details {
							background-image: url(../img/destination-logo-onedrive-small.svg);
							background-size: 16px;
							background-position: left;
							background-repeat: no-repeat;
							padding-left: 25px;
							font-weight: 500;
							color: #888888;
							margin-right: 3px;
						}

						.tpd-description {
							font-weight: 500;
							color: #888888;
						}
					}

					@media (max-width: 826px) {
						.sui-box-footer {
							flex-wrap: wrap;

							.snapshot-delete-backup {
								margin-left: 0;
							}

							.sui-content-right {
								justify-content: left !important;
								margin-top: 15px;
							}
						}
					}
				}
				.snapshot-restoration {
					display: none;
				}
				.snapshot-current-restoration {
					.snapshot-non-restoration {
						display: none;
					}
					.snapshot-restoration {
						display: block;
					}
				}
				.snapshot-current-restoration.snapshot-row {
					td {
						display: none;
						&.snapshot-restoration {
							display: table-cell;
						}
					}
				}

				.snapshot-icon-tooltip2 {
					&:before, &:after {
						opacity: 1;
    					backface-visibility: visible;
					}
				}

				.current-backup-details, .snapshot-restoration {

					p {
						color: #888888;
						font-size: 12px;
						font-weight: 500;
					}

					.progressbar-header {
						display: flex;
						justify-content: space-between;
						margin-bottom: 10px;
					}

					.progressbar-container.mobile {
						margin-bottom: 5px;
						margin-top: 12px;
					}

					.progress-title {
						width: calc(100% + 20px);
						display: flex;
						align-items: center;
						justify-content: flex-start;

						p {
							flex-grow: 0;
							flex-basis: 25%;

							&:first-child {
								margin-right: -16px;
							}

							span {
								display: none;
							}
						}
					}
					.progress-title.mobile {
						p {
							flex-basis: 100%;
						}
					}

					.progressbar-container.mobile {
						.progressbar {
							height: 100%;
							background-color: #17a8e3;
							border-radius: 5px 0 0 5px;
						}
						.progressbar.mobile-0 {
							display: none;
							width: 0;
							border-radius: 5px;
						}
						.progressbar.mobile-100 {
							display: none;
							width: 100%;
							border-radius: 5px;
						}
						.progress-circles {
							display: flex;
							align-items: center;
							justify-content: space-between;
							position: absolute;
							top: 0;
							width: 100%;
							height: 100%;

							.sui-tooltip {
								height: 100%;
								position: relative;
								top: -23px;
							}

							.circle {
								display: none;
								width: 16px;
								height: 16px;
								background-color: #e6e6e6;
								border-radius: 50%;
								text-align: center;
							}
							.circle.active {
								background-color: #17a8e3;
								&:before {
									font-family: wpmudev-plugin-icons;
									font-size: 8px;
									position: relative;
									top: -3.5px;
									speak: none;
									color: white;
									content: "(";
								}
							}
							.circle.invisible {
								display: block;
								visibility: hidden;
							}
						}
						.progress-circles.mobile {
							justify-content: flex-end;
						}
					}

					.current-backup-step-wrap:focus-visible{
						outline:none;
					}
					.sui-box:focus{
						border:none;
					}

				}

				.snapshot-restoration {
					.progress-title {
						width: 100%;
					}
				}

				.step-0 .on-step-0, .step-0 .lt-step-1, .step-0 .lt-step-2, .step-0 .lt-step-3, .step-0 .lt-step-4, .step-0 .lt-step-5,
				.step-1 .on-step-0, .step-1 .on-step-1, .step-1 .lt-step-2, .step-1 .lt-step-3, .step-1 .lt-step-4, .step-1 .lt-step-5,
				.step-2 .on-step-0, .step-2 .on-step-1, .step-2 .on-step-2, .step-2 .lt-step-3, .step-2 .lt-step-4, .step-2 .lt-step-5,
				.step-3 .on-step-0, .step-3 .on-step-1, .step-3 .on-step-2, .step-3 .on-step-3, .step-3 .lt-step-4, .step-3 .lt-step-5,
				.step-4 .on-step-0, .step-4 .on-step-1, .step-4 .on-step-2, .step-4 .on-step-3, .step-4 .on-step-4, .step-4 .lt-step-5,
				.step-5 .on-step-0, .step-5 .on-step-1, .step-5 .on-step-2, .step-5 .on-step-3, .step-5 .on-step-4, .step-5 .on-step-5,
				.step-0 .only-step-0, .step-1 .only-step-1, .step-2 .only-step-2, .step-3 .only-step-3, .step-4 .only-step-4, .step-5 .only-step-5
				{
					display: block !important;
				}

				.step-0 .progressbar-status {width: 0;}
				.step-1 .progressbar-status {width: 25%;}
				.step-2 .progressbar-status {width: 50%;}
				.step-3 .progressbar-status {width: 75%;}
				.step-4 .progressbar-status {width: 100%;}

				.snapshot-restoration.step-0 .progressbar-status {width: 0;}
				.snapshot-restoration.step-1 .progressbar-status {width: 20%;}
				.snapshot-restoration.step-2 .progressbar-status {width: 40%;}
				.snapshot-restoration.step-3 .progressbar-status {width: 60%;}
				.snapshot-restoration.step-4 .progressbar-status {width: 80%;}
				.snapshot-restoration.step-5 .progressbar-status {width: 100%;}

				.restoration-step {
					p {display: none;}
				}

            }
        }

		.snapshot-listed-backups, .snapshot-logs {
			.mobile-row {
				padding: 20px;

				.running-backup-desc {
					margin-top: 10px;
				}

				.sui-checkbox {
					display: inline-flex;
					vertical-align: inherit;
					margin-right: 8px;
				}
			}

			.snapshot-export-column {
				.snapshot-export-failure:after {
					white-space: nowrap;
				}
			}

			.snapshot-export-failure {
				margin-left: 5px;
			}

			.snapshot-export-icon {
				[class*="sui-icon-"] {
					&:before {
						font-size: 12px;
					}
				}
				&.snapshot-export-details-success {
					[class*="sui-icon-"] {
						margin-left: 3px;
						&:before {
							color: #1ABC9C;
						}

					}
				}
				&.snapshot-export-details-failure {
					[class*="sui-icon-"] {
						&:before {
							color: #FF6D6D;
						}

					}
				}
				&.snapshot-export-details-failure2 {
					display: none;
					&:before, &:after {
						opacity: 1;
						backface-visibility: visible;
					}
					[class*="sui-icon-"] {
						&:before {
							color: #FF6D6D;
						}

					}

				}
			}
		}

		.sui-box {
			.logs-loading {
				padding: 150px 30px;

				.sui-icon-loader {
					margin-right: 5px;
				}
			}
		}

		.snapshot-backups-settings {

			form#wps-settings {
				.sui-form-field > p {
					margin-top: 10px;

					small strong {
						color: #333333;
					}
				}
			}

			.snapshot-region-label {
				margin-bottom: 5px;
			}

			.snapshot-region-description {
				margin: 0 0 10px 27px;
			}

			.undo-exclusions {
				i{
					&::before {
						color: #17a8e3;
					}
				}
				&:hover {
					i {
						&::before {
							color: #1286b5;
						}
					}
				}
			}

			.snapshot-settings--db__method {
				.sui-side-tabs {
					margin-bottom: 0;
				}

				.sui-notice--mysqldump {
					margin-top: 15px;
				}
			}
		}

		.snapshot-export-backup-header {
			background-size: 16px;
			background-position: left 5px;
			background-repeat: no-repeat;
			padding-left: 20px;
			margin-right: 5px;
			display: block;

			&.snapshot-tooltip-hover {
				&:before, &:after {
					display: none;
				}
			}

			&.snapshot-s3-export-backup-header {
				background-image: url(../img/destination-logo-aws-small.svg);
			}

			&.snapshot-gdrive-export-backup-header {
				background-image: url(../img/destination-logo-gd-small.svg);
			}

			&.snapshot-dropbox-export-backup-header {
				background-image: url(../img/destination-logo-dropbox-small.svg);
			}

			&.snapshot-ftp-export-backup-header {
				background-image: url(../img/destination-logo-ftp-small.svg);
			}

			&.snapshot-sftp-export-backup-header {
				background-image: url(../img/destination-logo-ftp-small.svg);
			}

			&.snapshot-onedrive-export-backup-header {
				background-image: url(../img/destination-logo-onedrive-small.svg);
			}
		}

		.snapshot-notifications {
			.notification-options {
				margin-top: 30px;

				.sui-description {
					margin-bottom: 10px;
				}
				.sui-checkbox {
					padding-left: 13px;
				}
			}
		}

    }

	.logs-list {

		.sui-accordion-item-content {

			.log-items-container {
				max-height: 600px;
				overflow-y: auto;

				.sui-notice.no-warning, .sui-notice.no-error {
					display: none;
					margin: 0;

					p {
						margin: 0;
					}
				}
			}

			.sui-box-footer {
				justify-content: space-between;
				.flip-h:before {
					transform: scale(-1, 1);
				}
			}

		}

		.log-item {
			position: relative;
			padding-left: 23px;

			&__icon {
				position: absolute;
				left: 0;
				top: 5px;
			}

			&__content {
				font-size: 13px;
				color: #888888;
				font-weight: 500;
				line-height: 30px;
			}

			&.log-level-default {
				.log-item__icon {
					@include icon(before, page, true);
				}
			}
			&.log-level-error {
				.log-item__icon {
					@include icon(before, warning-alert, true);
					&:before {color: $notice-error-icon-color;}
				}
			}
			&.log-level-warning {
				.log-item__icon {
					@include icon(before, warning-alert, true);
					&:before {color: $notice-warning-icon-color;}
				}
			}
			&.log-level-info {
				.log-item__icon {
					@include icon(before, check-tick, true);
					&:before {color: $notice-success-icon-color;}
				}
			}
		}

		.sui-box {
			.log-loader {
				padding: 150px 30px;

				.sui-icon-loader {
					margin-right: 5px;
				}

				p {
					font-size: 15px;
				}
			}
		}

		.log-rows {
			.snapshot-loading {
				margin-bottom: 0;
			}
		}

	}

	.snapshot-mobile-title {
		margin: 20px 0 5px 0;
	}

	.sui-floating-notices {
		.sui-code-snippet-wrapper {
			.sui-code-snippet.snapshot-ips-snippet {
				margin-top: 10px;
				border: none;
				background-color: #fafafa;
			}
			.sui-button-icon {
				top: 7px;
				right: 5px;
				border-radius: 4px;
			}
		}
	}

}