.snapshot-page-dashboard {

	.sui-box.snapshot-dashboard-summary {

		.snapshot-backups-number {

			[class*="sui-icon-"] {
				top: 0;
				left: 0;
			}

		}

		.sui-summary-segment {

			.sui-list {
				padding-bottom: 15px;

				.sui-progress-bar {
					border-radius: 5px;
					width: 80px;
					height: 7px;
				}
			}

		}

		.used-space {
			margin-top: 4px;
			text-align: right;
			font-size: 12px;
			font-weight: 500;
			color: #aaaaaa;
		}

	}

	.snapshot-listed-backups, .snapshot-dashboard-destinations {
		.sui-table {
			thead {
				tr {
					th {
						height: 30px;
						margin-top: 30px;
					}
				}
			}

			.sui-table-item-title {
				.sui-description {
					display: none;
				}
			}
		}

		.snapshot-export-column, .snapshot-schedule-column {
			display: none;
		}

		.snapshot-export-backup-header {
			background-size: 16px;
			background-position: left;
			background-repeat: no-repeat;
			padding-left: 20px;
			margin-right: 5px;
			display: block;

			&.snapshot-tooltip-hover {
				&:before, &:after {
					display: none;
				}
			}

			&.snapshot-s3-export-backup-header {
				background-image: url(../img/destination-logo-aws-small.svg);
			}

			&.snapshot-gdrive-export-backup-header {
				background-image: url(../img/destination-logo-gd-small.svg);
			}

		}

		.snapshot-export-failure {
			margin-left: 5px;
		}
	}

    .snapshot-dashboard-backups {

		.snapshot-row.snapshot-failed-backup {
			border-left: 2px solid $notice-error-icon-color;
		}

        .sui-notice {
            margin-bottom: 10px;
        }

        .sui-message-content {
            max-width: 415px;
        }

		.sui-box-body, .snapshot-listed-backups, .snapshot-listed-backups-empty {
			display: none;
		}

		.sui-box-footer {
			display: none !important;
			justify-content: space-between;
		}

		.snapshot-backup-list-loader {
			padding-top: 144px;
			padding-bottom: 145px;

			.sui-icon-loader {
				margin-right: 5px;
			}

			p {
				font-size: 13px;
				color: #888888;
				font-weight: 500;
			}
		}

    }

	.snapshot-dashboard-hosting-backups {
		.snapshot-hosting-backup-list-loader {
			padding-top: 114px;
			padding-bottom: 115px;

			.sui-icon-loader {
				margin-right: 5px;
			}

			p {
				font-size: 13px;
				color: #888888;
				font-weight: 500;
			}
		}
	}

	.snapshot-listed-backups, .snapshot-listed-hosting-backups {
		.sui-icon-snapshot,
		.sui-icon-automate,
		.sui-icon-storage-server-data {
			&:before {
				color: #333333;
			}
		}

		.sui-icon-wpmudev-logo {
			&:before {
				color: #3174fa;
			}
		}
	}

    .snapshot-dashboard-destinations {

        .sui-table.sui-table-flushed {
            .sui-icon-wpmudev-logo {
                &:before {
                    color: #3174fa;
                }
            }
        }

		.row-icon .tooltip-container {
			padding-right: 45px;
		}

		.snapshot-loader {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 80px;
			border-top: 1px solid #e6e6e6;

			span.sui-icon-loader.sui-loading {
				position: relative;
				top: 2px;
			}
			.loader-text {
				margin-left: 10px;
				font-size: 13px;
				color: #888888;
				font-weight: 500;
			}
		}
    }
}