#snapshot-welcome-dialog,
#snapshot-welcome-dialog-configs {
	.sui-modal-slide {
		.sui-box {
			.sui-box-header {
				padding: 15px 60px 0;

				.sui-box-banner {
					height: 160px;
					margin-left: -60px;
					margin-right: -60px;
					margin-top: -15px;
					background-color: #f2f2f2;
					background-image: url(../img/snapshot-welcome-hero.svg);
					background-size: 135px;
					background-position: center bottom;
					background-repeat: no-repeat;

					&.snapshot-hidden-branding {
						background-image: url(../img/snapshot-welcome-unbranded.svg);
						background-position: center;
						background-color: #1283B0;
					}
				}
			}

			.hide-when-region-selected, .close-modal {
				&.snapshot-hidden-branding {
					&:hover, &:focus, &:active {
						background-color: transparent;
					}
					[class*="sui-icon-"] {
						color: #F2F2F2;
					}
				}
			}

			.sui-box-body {
				.apply-config--links {
					li {
						a {
							text-decoration: underline;
							font-size: 95%;

							&.applying{
								opacity: 0.3;
							}
						}

						.sui-tooltip {
							margin-left: 5px;
							margin-top: 3px;
							cursor: pointer;
						}
					}
				}
			}
		}
	}
	#snapshot-welcome-dialog-slide-1 .sui-box-body {
		padding-bottom: 50px;
	}
	#snapshot-welcome-dialog-slide-2 .sui-box-footer {
		padding-bottom: 20px;
	}
	#snapshot-welcome-dialog-slide-3 .sui-box-footer {
		padding-bottom: 50px;
	}

	#snapshot-welcome-dialog-slide-4 .sui-box-footer {
		padding-bottom: 50px;
	}

	.snapshot-get-started {
		margin-right: 0;
	}

	.hosting-backups-link {
		color: #888888;
	}
}

#snapshot-welcome-dashboard-dialog {
	.sui-modal-slide {
		.sui-box {
			.sui-box-header {
				padding: 15px 60px 0;

				.sui-box-banner {
					margin-left: -60px;
					margin-right: -60px;
				}
			}
			.sui-box-body {
				padding-bottom: 50px;
			}
		}
	}
}

#modal-snapshot-edit-schedule {

	.sui-box-banner {
		margin-bottom: 26px;
	}

	label.apply-exclusions {
		justify-content: center;
		margin-top: 30px;
		margin-bottom: 0px;
		small {
			color: #666666;
		}
	}

	.sui-button[type="submit"] {
		margin-top: 20px;
	}

	.create-backup-description {
		margin-bottom: 20px;
	}

	#snapshot-notice-tpd-schedule {
		&.sui-notice:last-child {
			margin-top: 10px;
		}
	}

	.sui-tag-pro {
		margin-left: 10px;
		cursor: pointer;
	}

}

#modal-snapshot-create-manual-backup {
	label.apply-exclusions {
		justify-content: center;
		margin-top: 30px;
		margin-bottom: 30px;
		small {
			color: #666666;
		}
	}

	.sui-button {
		margin-bottom: 20px;
	}

	#snapshot-notice-tpd-backup {
		text-align: left;
	}
}

#snapshot-modal-log {

	.sui-border-frame {
		height: 500px;
		margin: 0 0 20px;
		padding: 15px;
		background-color: #fafafa;
		overflow-y: auto;
	}

	.log-loader {
		display: none;
		width: fit-content;
		margin: 20px auto 0;
	}

	.log-item {
		color: #333333;
		font-size: 15px;
		line-height: 30px;
		font-weight: 500;

		&.log-level-error {
			color: #ff6d6d;
		}

		span.sui-icon-loader {
			margin-left: 5px;
			position: relative;
			top: 1px;
		}
	}

}

#modal-snapshot-restore-backup {

	input {
		padding-left: 40px;
	}

	.sui-icon-folder-open {
		position: relative;
		top: -33px;
		left: 15px;
	}

	.sui-box-body {
		padding-bottom: 20px;
	}

}

#snapshot-welcome-dialog {

	.hosting-backups-description {
		margin-bottom: 30px;

		p {
			color: #888888;
			font-size: 13px;
			line-height: 22px;

			&.list-header {
				color: #333333;
				line-height: 8px;

				.bullet {
					font-size: 30px;
					line-height: 0;
					position: relative;
					top: 6px;
					margin-right: 7px;
				}
			}
		}
	}

	.sui-tag-pro {
		margin-left: 10px;
		cursor: pointer;
	}

}

#snapshot-whats-new-modal {
	.sui-button-icon {
		.sui-icon-close:before {
			color: #333333;
		}
		&:hover, &:focus {
			.sui-icon-close:before {
				color: inherit;
			}
		}
	}

	.sui-box-banner {
		margin-bottom: 30px;

		/* Fix 1px white border in top modal corners */
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;
	}

	.list-header {
		color: #333333;
		line-height: 8px;
		font-size: 13px;

		.bullet {
			font-size: 30px;
			line-height: 0;
			position: relative;
			top: 6px;
			margin-right: 7px;
		}
	}
}

#snapshot-confirm-wpmudev-password-modal {
	.snapshot-google-login-button {
		background: #FFFFFF;
		border: 1px solid #DDDDDD;
		box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
		border-radius: 4px;
		font-weight: 500;
		font-size: 12px;
		height: 40px;
		line-height: 14px;
		letter-spacing: -0.25px;
		color: #333333;
		text-transform: initial;

		&:hover {
			color: #fff;
			background: #17A8E3;
			border: 1px solid #17A8E3;

			svg {
				path {
					fill: #fff;
				}
			}
		}
	}
}