.snapshot-page-destinations {
	.sui-modal {
		.sui-form-field {
			label {
				span {
					margin-left: 5px;
				}
			}

			.sui-description {
				.snapshot-directory-name {
					margin-left: 0;
				}
			}
		}
	}

	.snapshot-destinations-summary {

		@media (min-width: 783px) {
			&.sui-summary:not(.sui-unbranded):not(.sui-rebranded):not(.sui-summary-sm) {
				background-image: url(../img/snapshot-destination-hero.svg) !important;
				background-size: 226px 115px !important;
				background-position: 0px bottom !important;
				background-repeat: no-repeat !important;
			}

			.sui-summary-image-space {
				min-height: 143px !important;
				padding: 0 !important;
			}
		}

		.sui-summary-segment {

			.sui-list {
				padding-bottom: 15px;

				.sui-progress-bar {
					border-radius: 5px;
					width: 80px;
					height: 7px;
				}
			}

		}

		.used-space {
			margin-top: 4px;
			text-align: right;
			font-size: 12px;
			font-weight: 500;
			color: #aaaaaa;
		}

	}

	.snapshot-destinations {

		.destination-row {
			.sui-toggle {
				&:hover {
					cursor: pointer !important;
				}
			}
		}

		.sui-table {
			.sui-tag {
				margin-left: 10px;

				min-height: 12px;
				padding: 2px 8px 1px;
				border: 0;
				border-radius: 6px;
				font-size: 8px;
				line-height: 9px;
				text-align: center;
				text-transform: uppercase;
			}

			td {
				font-size: 13px;
				font-weight: 500;
				color: #888888;
			}

			.soon-destination {
				opacity: 0.55;
				background-color: #fafafa;
			}

		}

		.snapshot-loader {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 90px;
			padding-bottom: 30px;

			span.sui-icon-loader.sui-loading {
				position: relative;
				top: 2px;
			}
			.loader-text {
				margin-left: 10px;
				font-size: 13px;
				color: #888888;
				font-weight: 500;
			}
		}

		.api-error {
			width: 100%;
			padding-bottom: 30px;

			.sui-notice {
				margin-bottom: 10px;
			}
		}

		.sui-box-footer {
			padding-bottom: 0 !important;
		}

		.snapshot-destination-path {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}

	}

	.snapshot-destination-selectors {
		.sui-tag.sui-tag-pro {
			background-color: #17A8E3;
		}
	}

	.snapshot-expand-aws-instructions {
		color: #17A8E3;
		font-weight: 500;
		cursor: pointer;
	}

	.snapshot-error-select {
		border-bottom: 2px solid #FF6D6D !important;
	}

	.sui-side-tabs.snapshot-s3-selection>[data-panes]>*.sui-tab-boxed {
		border: none;
	}

	.sui-tab-boxed.snapshot-amazon-s3-tab, .sui-tab-boxed.snapshot-s3-compatible-tab {
		padding: 30px 0 0!important;
		margin-bottom: 30px!important;

		.snapshot-amazon-s3, .snapshot-s3-compatible {
			padding: 0 30px;

			span.sui-description {
				margin-top: -30px;
				margin-bottom: 30px;
			}
		}

		.snapshot-aws-credentials-howto, .snapshot-backblaze-credentials-howto, .snapshot-googlecloud-credentials-howto, .snapshot-digitalocean-credentials-howto, .snapshot-wasabi-credentials-howto, .snapshot-gd-credentials-howto {
			margin-top: 30px!important;
			margin-bottom: 20px!important;
			border-bottom-width: 1px!important;
			color: #888888;

			.sui-side-tabs>[data-panes]>*.sui-tab-boxed {
				border: 0;
				padding-top: 10px;
			}
		}
	}

	#label-s3-compatible-connection-provider {
		.sui-label-link {
			color: #17A8E3;
			font-weight: 500;
		}
	}

	a.sui-button.sui-button-lg {
		&.snapshot-connect-google {
			background-color: #fff;
			-webkit-box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			color: #333;
			font-size: 12px;
			text-transform: none;

			span.sui-icon-google-connect{
				height: 12px;
				background-image: url(data:image/png;base64,);
				background-image: -webkit-image-set(url(data:image/png;base64,) 1x,url(data:image/png;base64,) 2x);
				background-image: image-set(url(data:image/png;base64,) 1x,url(data:image/png;base64,) 2x);
				background-repeat: no-repeat;
				background-position: 50%;
				background-size: 12px 12px;

			}

			&:hover {
				background-color: #17a8e3;
				color: #fff;

				span.sui-icon-google-connect{
					background-image: -webkit-image-set(url(data:image/png;base64,) 1x,url(data:image/png;base64,) 2x);
				}
			}
		}

		&.snapshot-connect-dropbox {
			background-color: #fff;
			-webkit-box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			color: #333;
			font-size: 12px;
			text-transform: none;

			span.sui-icon-social-dropbox {
				&::before {
					color: #0062FF;
				}
			}

			&:hover {
				background-color: #0062FF;
				color: #fff;

				span.sui-icon-social-dropbox {
					&::before {
						color: #fff;
					}
				}
			}
		}

		&.snapshot-connect-onedrive {
			background-color: #fff;
			-webkit-box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			box-shadow: 0 0 4px 0 rgba(0,0,0,.2);
			color: #333;
			font-size: 12px;
			text-transform: none;

			span.sui-icon-onedrive-connect {
				height: 16px;
				background-image: url("data:image/svg+xml;base64,");
				background-repeat: no-repeat;
				background-position: 50%;
				background-size: 16px;
			}

			&:hover {
				background-color: #0062ff;
				color: #fff;

				span.sui-icon-onedrive-connect {
					background-image: url("data:image/svg+xml;base64,");
				}
			}
		}
	}

	.snapshot-configured-account {
		background-size: 27px 25px;
		background-position: 27px center;
		background-repeat: no-repeat;
		padding-left: 27px !important;
		&.snapshot-configured-account--dropbox {
			background-image: url(../img/dropbox-account.svg);
		}
		&.snapshot-configured-account--gdrive {
			background-image: url(../img/google-drive-account.svg);
		}
		&.snapshot-configured-account--ftp {
			background-image: url(../img/icon-ftp.svg);
		}
		&.snapshot-configured-account--onedrive {
			background-image: url(../img/onedrive-account.svg);
		}

		.snapshot-configured-account-email {
			padding-left: 65px !important;
		}
	}

	.sui-form-field.loading {
		.select2-selection:before {
			display: block;
			width: 40px;
			animation: spin 1.3s linear infinite;
			font-family: wpmudev-plugin-icons;
			font-size: 15px;
			line-height: 15px;
			content: "N";
			text-align: center;
			speak: none;
		}
	}
}
